/*动态更新高考年份部分没做*/  
function Show_Time(props) {
  if (!props.warn) {
    return null;
  }
  return (
    <div className="Show_Time">
    <Clock/>
    </div>
  );
}


class Clock extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        date: new Date(),
        gaokaotime:new Date('2020/07/07 09:00'),
        year:new Date().getFullYear(),
        month:new Date().getMonth(),
        day:new Date().getDay()};
  }
  componentDidMount() {
    this.timerID = setInterval(
      () => this.tick(),
      1000
    );
  }
  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  tick() {
    this.setState({
      date: new Date()
    });
  }
  render() {
    return (
      <div className="count">
        <h2>现在是 {this.state.year}年{this.state.month}月{this.state.day}日{this.state.date.toLocaleTimeString()}.</h2>
        <h1>距离{this.state.year}年高考还有。。。。</h1>
        <h1>{parseInt(((this.state.gaokaotime.getTime() - this.state.date.getTime())/1000)/(3600*24))}天
        {parseInt((((this.state.gaokaotime.getTime() - this.state.date.getTime())/1000)%(3600*24))/3600)}小时
        {parseInt((((this.state.gaokaotime.getTime() - this.state.date.getTime())/1000)%3600)/60)}分钟
        {parseInt(((this.state.gaokaotime.getTime() - this.state.date.getTime())/1000)%60)}秒</h1>
        <h1>还不滚去学习!!!!!!!!!!</h1>
      </div>
    );
  }
}

