function Basic_info (){
  let history = useHistory();
  function jump_beikao(){
    history.push("/School/三亚学院/Major/其它");
  }
  function jump_zhuanye(){
    history.push("/School/复旦大学");
  }
  function jump_kaoyan(){
    history.push("/KaoYan");
  }
    return(
    <div className="basic-info">
      <button className="beikao" onClick={jump_beikao}>备考指南</button>
      <button className="zhuanye" onClick={jump_zhuanye}>专业测试</button>
      <button className="kaoyan" onClick={jump_kaoyan}>考研专区</button>
    </div>
    );
}


class Hide_butt extends React.Component{
  constructor(props) {
    super(props);
    this.state = {showtime: true}
    this.handle_Time = this.handle_Time.bind(this);
  }
  handle_Time() {
    this.setState(prevState => ({
      showtime: !prevState.showtime
    }));
  }
  render(){
    return (
      <div className="Time">
      <button onClick={this.handle_Time }>
      {this.state.showtime ? 'hide' : 'show'}
    </button>
    <Show_Time warn={this.state.showtime} />
    </div>
    )

  }
}

class School_location extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      value: '',
      shanghai:false,
      zhejiang:false,
      jiangsu:false,
      beijing:false,
      benke:false,
      zhuanke:false,
      S985:false,
      S211:false,
      Sgood:false,
      Mgood:false,
      enter_handle:false,
      choose_major:false,
      search_value:[],
      update:false,
      type:0

    }
    this.change_box1=this.change_box1.bind(this);
    this.change_box2=this.change_box2.bind(this);
    this.change_box3=this.change_box3.bind(this);
    this.change_box4=this.change_box4.bind(this);
    this.change_box5=this.change_box5.bind(this);
    this.change_box6=this.change_box6.bind(this);
    this.change_box7=this.change_box7.bind(this);
    this.change_box8=this.change_box8.bind(this);
    this.change_box9=this.change_box9.bind(this);
    this.change_box10=this.change_box10.bind(this);
    
  }
  componentDidMount() {
    // 默认值
    this.setState({
      value: this.props.value || ''
    })
  }
  ChangeHandle(e) {
    // 监控变化
    this.setState({
      value: e.target.value
    })
  }
  KeyUpHandle(e) {
    // 监控 enter 事件
    if (e.keyCode !== 13) {
      return
    }
    // 加验证
    if (e.target.value) {
      this.setState({
        enter_handle:true
      })
    }else {
      alert('请输入关键字')
    }
  }

  change_state(){
    this.setState ({
      choose_major:true
     });

  }
  change_box1(){
    this.setState(prevState => ({
      shanghai: !prevState.shanghai
    }));
  }
  change_box2(){
    this.setState(prevState => ({
      zhejiang: !prevState.zhejiang
    }));
  }
  change_box3(){
    this.setState(prevState => ({
      jiangsu: !prevState.jiangsu
    }));
  }
  change_box4(){
    this.setState(prevState => ({
      beijing: !prevState.beijing
    }));
  }
  change_box5(){
    this.setState(prevState => ({
      benke: !prevState.benke
    }));
  }
  change_box6(){
    this.setState(prevState => ({
      zhuanke: !prevState.zhuanke
    }));
  }
  change_box7(){
    this.setState(prevState => ({
      S985: !prevState.S985
    }));
  }
  change_box8(){
    this.setState(prevState => ({
      S211: !prevState.S211
    }));
  }
  change_box9(){
    this.setState(prevState => ({
      Sgood: !prevState.Sgood
    }));
  }
  change_box10(){
    this.setState(prevState => ({
      Mgood: !prevState.Mgood
    }));
  }
  convey_info(){
    this.setState ({
      update:true
     });

      let Data={"input":this.state.value,"985":this.state.S985,"211":this.state.S211,"上海":this.state.shanghai,"浙江":this.state.zhejiang,"江苏":this.state.jiangsu,
                 "北京":this.state.beijing,"本科":this.state.benke,"专科":this.state.zhuanke,"一流专业":this.state.Mgood,"一流高校":this.state.Sgood};

                  fetch('/Home',{
                        method:'post',
                        headers:{
                            'Accept':'application/json,text/plain,*/*',/* 格式限制：json、文本、其他格式 */
                            'Content-Type':'application/x-www-form-urlencoded'/* 请求内容类型 */
                        },
                        body:JSON.stringify(Data)
                    }).then((response)=>{
                        return response.json()
                    }).then((data)=>{
                      let list_school=[];
                      if (data["type"]==1){
                      for(let i=0;i<data["result"].length;i++){
                        list_school.push(data["result"][i][0])
                        list_school.push(data["result"][i][1])
                            }
                      }
                      if (data["type"]==0){
                        for(let i=0;i<data["result"].length;i++){
                          list_school.push(data["result"][i])
                       }

                      }
                      this.setState({
                        search_value: list_school
                      });
                      this.setState({
                        type: data["type"]
                      });
                      console.log(this.state.search_value)
                    }).catch(function(error){
                        console.log(error)
                    })
      
}
  
  render(){
    return (

      <div>
        <input
        className="search-input"
        type="text"
        placeholder="请输入学校或专业（如：上海交通大学 如：英语）"
        onChange={this.ChangeHandle.bind(this)}
        onKeyUp={this.KeyUpHandle.bind(this)}
        value={this.state.value}/>
        <div className="location_tag">
          <div class="ant-row-loc">
              <div class="ant-slogan-loc ant-slogan-success">
               <span class="ant-slogan-message">院校所在地</span>
               <span class="ant-alert-description"></span>
              </div>
          </div>
          <div class="selectBoxLoc">
            <input type="checkbox" onClick={this.change_box1}/>上海
          </div>
          <div class="selectBoxLoc">
            <input type="checkbox" onClick={this.change_box2}/>浙江
          </div>
          <div class="selectBoxLoc">
            <input type="checkbox" onClick={this.change_box3}/>江苏
          </div>
          <div class="selectBoxLoc">
            <input type="checkbox" onClick={this.change_box4}/>北京
          </div>
        </div>
        <div className="level_tag">
          <div class="ant-row-level">
              <div class="ant-slogan-level ant-slogan-success">
               <span class="ant-slogan-message">院系特性</span>
               <span class="ant-alert-description"></span>
              </div>
          </div>
          <div class="selectBoxLevel">
            <input type="checkbox" onClick={this.change_box7}/>985
          </div>
          <div class="selectBoxLevel">
            <input type="checkbox" onClick={this.change_box8}/>211
          </div>
          <div class="selectBoxLevel">
            <input type="checkbox" onClick={this.change_box9}/>一流高校
          </div>
          <div class="selectBoxLevel">
            <input type="checkbox" onClick={this.change_box10}/>一流专业
          </div>
        </div>
        <button onClick={this.convey_info.bind(this)} class="ant-btn login-form-button ant-btn-primary ant-btn-lg">
          <span>开始筛选</span>
        </button>
        <Show_result result={this.state.search_value} indicate={this.state.update} type={this.state.type}/>
      </div>
    )
  }
}


function Show_result(props){
 
  if (props.indicate==true){
  let list;
  list=props.result;
  var link= [];
  if (props.type==1)
   for (var i = 0; i <(list.length)/2; i++) {
       link.push(<div ><Link to={"/School/"+list[2*i]+"/Major/"+list[2*i+1]}>{list[2*i]+"-"+list[2*i+1]}</Link></div>);
   }
  if (props.type==0){
    for (var i = 0; i <(list.length); i++) {
      link.push(<div ><Link to={"/School/"+list[i]}>{list[i]}</Link></div>);
  } 
  }

  return(
    
    <div>
      {link}
    </div>  
)
  }
  else{
    return null
  }
}


