// main info of the dynamic panel
function Show_basic (props){
    let {school_name}=useParams();
    let {major}=useParams();
    const[info, setInfo]=useState(0);
    useEffect(()=>{
        let Data={"type":1}
        fetch('/School/'+school_name+'/Major/'+major,{
            method:'post',
            headers:{
                'Accept':'application/json,text/plain,*/*',/* 格式限制：json、文本、其他格式 */
                'Content-Type':'application/x-www-form-urlencoded'/* 请求内容类型 */
            },
            body:JSON.stringify(Data)
        }).then(res=>res.json()).then(data=>{
            setInfo(data["result"])
    
        });
    },[]);
    if (props.indicator==1) {
        return (
            <div className="panel">
            {info}
            </div>
          );
      }
    if(props.indicator==2){
        return (
            <div className="panel">
           {/* <p>数据接口{school_name}</p>
            <p>学校:props.major{major}</p>
            专业具体信息：
            <p>哲学是人文科学领域的基础学科...</p> */}
            </div>
          );
    }
    if(props.indicator==3){
        return (
            <div className="panel">
           {/*  <p>数据接口{school_name}</p>
            <p>学校:props.major{major}</p>
            专业课程信息：
            <p>哲学导论 哲学原理导读。。。。</p> */}
            </div>
          );
    }
    if(props.indicator==4){
        return (
            <div className="panel">
            {/* <p>数据接口:props.school_name{school_name}</p>
            <p>学校:props.major{major}</p>
            毕业去向：
            <p>具体数据？</p> */}
            </div>
          );
    }
    if(props.indicator==5){
        return (
            <div className="panel">
            {/* <p>数据接口:props.school_name{school_name}</p>
            <p>学校:props.major{major}</p>
            类似院系：
            <p>图表</p> */}
            </div>
          );
    }
    else{
        return null
    }
}

function compare(v1,v2){
    if (v1[0]<v2[0]){
        return 1
    }
    else{
        return 0
    }
}

function Zhihu_chart (props){
    //Get all comment information 
    let {school_name}=useParams();
    let {major}=useParams();
    const[comment, setComment]=useState(0);
    useEffect(()=>{
        fetch('/School/'+school_name+'/Major/'+major).then(res=>res.json()).then(data=>{
           let list_comment=[];
           for(let i=0;i<data["result"].length;i++){
               list_comment.push(data["result"][i])
           }
           setComment(list_comment)
        });
    },[]);
    var link=[]

    //if no comment
    if (comment==null){
        return (
            <div>
                暂无信息
            </div>
        )
    }
    for (var i=0;i<comment.length;i++){
        if (!comment[i][2]){
            return (
                <div>
                    暂无信息
                </div>
            )

        }
        var list=comment[i][2].split("");
        var All_HighLine=[]

        for (var j=9; j<16;j++){
            if (comment[i][j]!=null){
                var pos=comment[i][j].split(/\)|\(|\,|" "/)
                var temp=[]
                temp.push(j)
                for(var k=0;k<pos.length;k++){
                    if (temp.length==3){
                        All_HighLine.push(temp)
                        temp=[]
                        temp.push(j)
                    }
                    if (temp.length<3){
                        var result=parseInt(pos[k])
                        if (!isNaN(result)){
                            temp.push(result)
                        }
                    }
                }
            }
        }
        if (All_HighLine.length!=0){
            var nodeOFtag=[]
            console.log(props.indicator)
            console.log(All_HighLine)
            for (var count=0;count<All_HighLine.length;count++){
                if  (All_HighLine[count][0]==props.indicator){
                    var startPoint=All_HighLine[count][1]
                    var endPoint=All_HighLine[count][2]
                    var len=endPoint-startPoint
                    if (nodeOFtag.length!=0){
                        var accmu=0;
                        for(var k=0;k<nodeOFtag.length;k++){
                            if (startPoint>nodeOFtag[k][0]){
                               accmu=accmu+nodeOFtag[k][1]-1
                            }
                            else{
                                break
                            }
                        }
                        startPoint=startPoint-accmu
                        endPoint=endPoint-accmu
                    }
                    var mid=list.splice(startPoint,len)
                    if (props.indicator==9){
                        list.splice(startPoint,0,<span className="highLight h1">{mid}</span>)
                    }
                    if (props.indicator==10){
                        list.splice(startPoint,0,<span className="highLight h2">{mid}</span>)
                    }
                    if (props.indicator==11){
                        list.splice(startPoint,0,<span className="highLight h3">{mid}</span>)
                    }
                    if (props.indicator==12){
                        list.splice(startPoint,0,<span className="highLight h4">{mid}</span>)
                    }
                    if (props.indicator==13){
                        list.splice(startPoint,0,<span className="highLight h5">{mid}</span>)
                    }
                    if (props.indicator==14){
                        list.splice(startPoint,0,<span className="highLight h6">{mid}</span>)
                    }
                    if (props.indicator==15){
                        list.splice(startPoint,0,<span className="highLight h7">{mid}</span>)
                    }
                    nodeOFtag.push([startPoint,len])

                    nodeOFtag.sort(compare)
                    console.log(mid)
                }
            }
    }
        link.push(<div className="card">
        <h3>{comment[i][1]}</h3>
        <h5>{comment[i][7]}</h5>
        <p>作者</p>
        <p>{list}</p>
        <h5>{comment[i][5]}</h5>
        </div>)

        
    }
//return all comment
return (
    <div>
    {link}
    </div>
  );
}

class Dynamic_Panel extends React.Component{
    constructor(props){
        super(props);
        this.state={
           indicator:1,
           school_name:"",
           major:""
        }
    }
    change_state(value,event){
        this.setState ({
           indicator:value
          });
        }

    render(){
        return(
            <div>
            <div className="topnav">
            <a href="#" onClick={this.change_state.bind(this,1)}>基本信息</a>
            {/* <a href="#" onClick={this.change_state.bind(this,2)}>专业介绍</a>
            <a href="#" onClick={this.change_state.bind(this,3)}>主要课程</a>
            <a href="#" onClick={this.change_state.bind(this,5)}>类似院系</a> */}
            </div>
            <Show_basic indicator={this.state.indicator}/>
            </div>
        )
    }    
}
function TagAndComment(){
    return (
        <div>
            <Tag_extraction />
            <Zhihu_chart/>
        </div>
    )
}

function Major_Page (){
    let history = useHistory();
    let {school_name}=useParams();
    let {major}=useParams();
    function goback(){
        history.push('/Home');
    }
    function back2shcool(){
        history.push('/School/'+school_name);
    }
    return(

        <div>
            <button onClick={goback}>返回</button>
            <button onClick={back2shcool}>{school_name}</button>
            <div className="Title">
            <h1>{school_name}-{major}</h1>
            </div>
           
            <Dynamic_Panel />
            <TagAndComment/>
            
         </div>
)
}
export default Major_Page



