function Show_Dy(props){
    let history = useHistory();
    const[intro,setintro]=useState(0)
    const[major,setmajor]=useState(0)
    const[comment,setcomment]=useState(0)
    const[majors,setmajors]=useState(0)
    let {school_name} = useParams();
    function jump(major){
        history.push('/School/'+school_name+'/Major/'+major);
    }
    useEffect(()=>{
        fetch('/School/'+school_name).then(res=>res.json()).then(data=>{
            setintro(data.intro);
            setmajor(data.main_major);
            let list_comment=[];
            for(let i=0;i<data["comment"].length;i++){
               list_comment.push(data["comment"][i])
            }
            setcomment(list_comment);
            let list_major=[]
            list_major=data["majors"].split(/\)|\(|,|" "|\'|\]|\[|\'/)
            console.log(list_major)
            setmajors(list_major);
        });

    },[]);
    var link=[]
    for (var i=0;i<comment.length;i++){
    link.push(<div className="card">
        <h3>{comment[i][1]}</h3>
        <h5>{comment[i][7]}</h5>
        <p>作者</p>
        <p>{comment[i][2]}</p>
        <h5>{comment[i][5]}</h5>
        </div>)
    }

    if (props.indicator==1){
        return (
            <div className="panel">
                {intro}
            </div>
          );
    }
    if (props.indicator==2){
        return (
            <div className="panel">
                {major}
            </div>
          );
    }
    /* if (props.indicator==3){
        return (
            <div>
            <p>数据接口:school_name{school_name}</p>
            毕业生质量：表现形式-> 饼图，具体数据来源？未知
            </div>
          );
    } */
    if (props.indicator==4){
        return(
        <div className="panel">
          {link}
        </div>
        );
        
    }
    // if (props.indicator==5){
    //     return(
    //     <div className="panel">
    //     <p>{school_name}</p>
    //     类 似 高 校
    //     </div>
    //     );
    // }
    // if (props.indicator==6){
    //     console.log(majors)
    //     let link=[]
    //     for(let i=0;i<majors.length;i++){
    //         let k='/School/'+school_name+'/Major/'+majors[i]
    //         link.push(<a href={k}>{majors[i]}</a>)
    //      }
    //     return(
    //         <div>
    //             {link}
    //         </div>
    //     );
    // }
    if (props.indicator==6){
        let link=[]
        for(let i=0;i<majors.length;i++){
            if(majors[i] != "其它"){
                link.push(<div >&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<Link to={"/School/"+school_name+"/Major/"+majors[i]}>{majors[i]}</Link><br /></div>)
            }
            else{continue;}
            
        }
        return(
            // <div>
            //     <a>aaa</a>
            //     <a>bbb</a>
            // </div>
            <div class="left">
                {link}
            </div>
        );
    }
}
//the structure of dynamic panel
class Dynamic_Panel extends React.Component{
    constructor(props){
        super(props);
        this.state={
            indicator:1
        }
    }
    change_state(value,event){
        this.setState ({
           indicator:value
          });
    }
    render(){
        return(
        <div>
            <div class="topnav">
            <a href="#" onClick={this.change_state.bind(this,1)}>学校简介</a>
            <a href="#" onClick={this.change_state.bind(this,2)}>院系设置</a>
          {/*   <a href="#" onClick={this.change_state.bind(this,3)}>毕业质量</a> */}
            <a href="#" onClick={this.change_state.bind(this,4)}>热点讨论</a>
            {/* <a href="#" onClick={this.change_state.bind(this,5)}>类似高校</a> */}
            <a href="#" onClick={this.change_state.bind(this,6)}>具体专业</a>
            </div>
            <Show_Dy indicator={this.state.indicator} school_name={this.props.school_name}/>
        </div>
        )
    }    
}

