//main struction of tje 
function School_Page(){
    let history = useHistory();
    let {school_name}=useParams();
    function goback(){
        history.push('/Home');
    }

return(
        <div className="body">
           <button onClick={goback}>返回</button>
            <div className="Title">
            <h1>{school_name}</h1>
            {/* <Tag_features /> */}
            
            </div>
            <Dynamic_Panel />

         </div>
)
}
export default School_Page



