import React, { Component } from 'react';
import logo from './logo.svg';
import Home from "./Home";
import School_Page from "./SchoolPage";
import Major_Page from "./MajorPage";
import HomePage from "./HomePage";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useHistory,
  useLocation,
  useParams
} from "react-router-dom";


export default function RouteMap() {
  return (
    <Router>
      <ModalSwitch />
    </Router>
  );
}

function ModalSwitch() {
  let location = useLocation();

  // This piece of state is set when one of the
  // gallery links is clicked. The `background` state
  // is the location that we were at when one of
  // the gallery links was clicked. If it's there,
  // use it as the location for the <Switch> so
  // we show the gallery in the background, behind
  // the modal.
  let background = location.state && location.state.background;

  return (
    <div>
      <Switch>
        <Route path="/" exact children={<HomePage/>}/>
        <Route path="/Home" exact children={<Home />} />
        <Route path="/School/:school_name" exact children={<School_Page />} />
        <Route path="/School/:school_name/Major/:major" exact children={<Major_Page />} />
      </Switch>
    </div>
  );
}



